@mixin primaryInput {
  border: 0.1rem solid var(--color-primary);
  padding: 1.7rem 1.6rem;
}

@mixin primaryInputFocused {
  border-width: 0.2rem;
}

@mixin validationErr {
  display: block;
  font-size: var(--font-size-12);
  color: var(--color-red-err);
  margin-top: 0.5rem;
}

@mixin submitSuccessMsg {
  background-color: var(--color-green-bg);
  padding: 1.5rem;
  color: var(--color-dark-text);
  line-height: 2.5rem;
  border: 0.1rem solid var(--color-light-green-border);
  margin-top: 7rem;
}

@mixin overlayWrapper($top: 0, $bg: var(--color-dark-overlay)) {
  position: fixed;
  left: 0;
  top: $top;
  z-index: 2;
  width: 100%;
  height: 100vh;
  background-color: $bg;
  transition: var(--transition-timing-sec);
}
