:root {
  --font-size-64: 6.4rem;
  --font-size-42: 4.2rem;
  --font-size-32: 3.2rem;
  --font-size-27: 2.7rem;
  --font-size-24: 2.4rem;
  --font-size-20: 2rem;
  --font-size-18: 1.8rem;
  --font-size-16: 1.6rem;
  --font-size-15: 1.5rem;
  --font-size-14: 1.4rem;
  --font-size-13: 1.3rem;
  --font-size-12: 1.2rem;
  --font-size-11: 1.1rem;
  --font-size-10: 1rem;
  --font-family-inter: 'Inter', sans-serif;
  --font-weight-regular: 400;
  --font-weight-bold: 700;

  --color-primary: #212121;
  --color-gray-hover: #757575;
  --color-darkest-gray: #666666;
  --color-light-gray: #f5f5f5;
  --color-light: #ffffff;
  --color-light-gray-bg: #f2f2f2;
  --color-light-gray-divider: rgba(33, 33, 33, 0.102);
  --color-light-gray-hero: #fbfbfb;
  --color-dark-gray-link: #716f6f;
  --color-dark-gray-arrow: #888888;
  --color-dark: #000000;
  --color-dark-overlay: #000000e6;
  --color-dark-text: #333333;
  --color-dark-red: #970000;
  --color-red-mandatory: #ff0000;
  --color-red-err: #ff6161;
  --color-dark-border: rgb(17, 17, 17);
  --color-dark-cart-shadow: #0000001f;
  --color-dark-sidebar-shadow: #00000012;
  --color-light-green-border: #b4d39b;
  --color-green-bg: #e0ffc7;

  --primary-horizontal-padding: 1.5rem;
  --secondary-horizontal-padding: 4rem;
  --width-content: 114rem;

  --transition-timing-sec: 0.3s;
}
