.product-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  gap: 3rem;
}

.product-category-list__no-data {
  text-align: center;
}

/* Start Responsive */
@media only screen and (max-width: 985px) {
  .product-list {
    justify-content: center;
  }
}

/* End Responsive */
